.Logo {
  height: 50px;
}

.LogoLabel {
  color: white;
  font-size: 20px;
  margin-left: 15px;
  display: inline-block;
}

.FixedNavigation {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10000;
}

.ClearNavigation {
  height: 77px;
  width: 100%;
  background-color: rgb(214, 120, 13);
  position: relative;
  z-index: -1;
}
.NavigationContainer {
  width: 100%;
  background-color: red;
  position: relative;
  display: table;
}
